import ListFlexItem from '@/components/list_flex_item.vue';
import EditTime from '@/components/edit_time.vue';
import EditSelect2 from '@/components/edit_select2.vue';

export default {
  components: {
    ListFlexItem,
    EditTime,
    EditSelect2
  },
  data () {
    return {
      inputValue: '',
      isInputFocused: false,
      isResultShow: false,
      isFilterShow: false,
      filterParams: {
        period: '',
        StartTime: '',
        EndTime: '',
        operationType: '',
        workType: '',
        planStatus: '',
        railwayBureau: ''
      },
      cellsData: [
        {
          firstLeft: '01月12日计划',
          firstRight: '跟车作业',
          secondLeft: '2018-11-29 16:47:22',
          isOnline: true,
          url: '/onlinePlan',
          isFinish: false,
          id: 1
        },
                {
          firstLeft: '01月12日计划',
          firstRight: '调查作业',
          secondLeft: '2018-11-29 16:47:22',
          isOnline: true,
          url: '/onlinePlan',
          isFinish: true,
          id: 1
        },
                {
          firstLeft: '01月12日计划',
          firstRight: '走访',
          secondLeft: '2018-11-29 16:47:22',
          isFinish: true,
          isOnline: false,
          url: '/offlinePlan',
          id: 2
        },
        {
          firstLeft: '01月12日计划',
          firstRight: '跟车作业',
          secondLeft: '2018-11-29 16:47:22',
          isFinish: false,
          isOnline: true,
          url: '/onlinePlan',
          id: 2
        },
        {
          firstLeft: '01月12日计划',
          firstRight: '调查作业',
          secondLeft: '2018-11-29 16:47:22',
          isOnline: true,
          url: '/onlinePlan',
          isFinish: false,
          id: 3
        },
        {
          firstLeft: '01月12日计划',
          firstRight: '走访',
          secondLeft: '2018-11-29 16:47:22',
          isOnline: false,
          url: '/offlinePlan',
          isFinish: true,
          id: 3
        }
      ],
      inputParams1: [
        {
          title: '开始日期',
          default_time: '请选择计划开始日期'
        },
        {
          title: '结束日期',
          default_time: '请选择计划结束日期'
        }
      ],
      inputParams2: [
        {
          title: '作业类型',
          list: [['打磨作业', '焊接作业', '组装作业']],
          val: [],
          placeholder: '请选择作业类型'
        },
        {
          title: '工作类型',
          list: [['巡视', '清点', '巡查']],
          val: [],
          placeholder: '请选择工作类型'
        },
        {
          title: '计划状态',
          list: [['未完成', '已完成']],
          val: [],
          placeholder: '请选择计划状态'
        },
        {
          title: '工作路局',
          list: [['北京铁路局', '上海铁路局', '山西铁路局']],
          val: [],
          placeholder: '请选择工作路局'
        }
      ],
    }
  },
  methods: {
    focusInput () {
      let input = document.getElementById('search_input')
      input.focus()
      this.isInputFocused = true
    },
    blurInput () {
      if (!this.inputValue.trim()) this.cancelSearch()
    },
    onInput () {
      this.isResultShow = this.inputValue.trim() ? true : false
    },
    cancelSearch () {
      this.isInputFocused = false
      this.isResultShow = false
      this.inputValue = ''
    },
    clearSearch () {
      this.isResultShow = false
      this.inputValue = '' 
    },
    resetForm () {
      this.inputParams2.forEach((item, index) => {
        item.val = []
      })
      this.inputParams1[0].default_time = '请选择计划开始日期'
      this.inputParams1[1].default_time = '请选择计划结束日期'
      this.filterParams.period = ''
    }
  }
}



